import {
    user as UserApi
} from 'config/request.js';

import {
    ListData
} from 'common/';


module.exports = {
    name: 'list',
    components: {
        ListData
    },
    data() {
        return {
            list: [],
            fields: [{
                key: 'username',
                label: ''
            }, {
                key: 'email',
                label: ''
            }, {
                key: 'create_time',
                label: '',
                sort: true
            }, {
                key: 'sex',
                label: '',
                formatter: function(item) {
                    return item.sex == 1 ? '' : (item.sex == 2 ? '' : '');
                },
                filter_list: [{
                    text: '',
                    value: 1
                }, {
                    text: '',
                    value: 2
                }, {
                    text: '',
                    value: 3
                }],
                filter_method: function(value, item) {
                    return item.sex == value;
                },
                filter_multiple: true,
            }, {
                key: 'status',
                label: '',
                formatter: function(item) {
                    return item.status == 1 ? '' : '';
                },
                filter_list: [{
                    text: '',
                    value: 1
                }, {
                    text: '',
                    value: 2
                }],
                filter_method: function(value, item) {
                    return item.status == value;
                },
                filter_multiple: false,
            }],
            btn_info: {
                // show: false,
                label: 'Actions',
                width: 300,
                // delete: false,
                // select: false,
                // update: false,
                list: [{
                    text: '',
                    fn_type: 'access'
                }, {
                    text: '',
                    fn_type: 'status'
                }]
            }
        }
    },
    methods: {
        /**
         * 
         */
        getList() {
            var data = {};
            UserApi.selectUser.call(this, data, (data) => {
                this.list = data.list;
            });
        },

        /**
         * CheckBox
         * 
         */
        onChangeCheckbox(ids, datas) {

        },

        /**
         * CheckBox
         * 
         */
        onChangeBoxObj(opts) {
            console.log(opts);
        },

        /**
         * 
         */
        onGetInfo(opts) {
            console.log(opts);
        },

        /**
         * 
         */
        onDelete(opts) {
            console.log(opts);
        },
    },
    mounted() {
        this.getList();
    },
    watch: {
        '$route' (to, from) {

        }
    }
}


